            commentary: '眼の解剖において、中心窩は視力の中心であるため、動脈は存在しない。眼動脈は視神経管内を走行し、網膜中心動脈などに分岐する。視神経の数は桿体細胞が錘体細胞よりも多い。緑内障は色素上皮細胞ではなく視細胞が障害される疾患である。 ときに、眼の周りの強い骨ではなく、眼球そのものに力が加わる形で打撃を受けることがあります（ゴルフボールや拳などの小さな物体がぶつかったときなど）。この場合、眼球への圧力は眼窩の壁に伝わります。この圧力により、眼窩の最も脆弱な部分の1つである、眼球内壁の下の骨（眼窩底）が折れることがあります。顔面への直接的な打撃によっても同じ骨折を引き起こす力が伝わることがあります。このタイプのけがを吹き抜け骨折と呼びます。ときには、眼窩内の一部（例えば、眼球に付着する筋肉など）が、骨折部から押し出され、骨と骨の間にはさまってしまいます。これは10代の若者や若年成人で最もよくみられ、緊急に修復する必要があります。',
