            choices:['動眼神経麻痺','Horner症候群','Marcus Gunn症候群','顔面神経麻痺','重症筋無力症'],
            answerImg: [],
            answer: '動眼神経麻痺',
            commentary: '眼瞼下垂は何らかの原因によって上眼瞼（上のまぶた）が下がってくる状態のことです。それにより上方の視野が狭く感じられたり、外見が悪くなったりする不都合が生じます。上眼瞼の挙上には上眼瞼挙筋（動眼神経支配）とミュラー筋（別名:瞼板筋、交感神経支配）の収縮が携わっており、これらどちらかの筋肉やそれを支配する神経の機能が落ちることで起こります。原因は大きく先天性と後天性にわけられます。 動眼神経は、瞳孔（どうこう）の動きもコントロールもしています。 このため、動眼神経が麻痺すると、瞳孔が散大してしまい、瞳孔が収縮しなくなります。 瞳孔が収縮しないため、眼球に入ってくる光の量が必要以上に多くなり、電球の光や太陽の光を今までよりもまぶしく感じるようになります。 ホルネル症候群(Horner症候群)では、顔の片側において、まぶたが垂れ下がり、瞳孔が小さくなり（収縮）、発汗が減少します。 Marcus Gunn症候群/現象とは、顎の動きに合わせてまぶたが動きます。共同運動とは複数の筋肉が同時に収縮すること。つまり顎の動き（外側翼突筋、内側翼突筋の収縮）に合わせて眼瞼挙筋が収縮（まぶたが挙上）するのです。顎の筋肉を動かす運動神経（三叉神経）の枝があやまって眼瞼挙筋に到達してしまうのが原因とされています。顔面神経麻痺では通常眼瞼下垂を伴わないが、顔面神経麻痺の後遺症で麻痺側の眉毛の位置が下がり、上まぶたの皮膚が覆いかぶさることで視野が狭くなります。 また、まぶたが閉じにくくなり、眼が乾燥したり炎症を起こすことがあります。 ものが二重に見える、という症状を認めるときには重症筋無力症（myasthenia gravis, MG）という病気がかくれている場合があります。MGでは眼の症状が比較的高頻度にあらわれます。その主な症状は眼瞼下垂（まぶたがさがってくる、だるい）、複視（ものが二重にみえる）です。このように症状が眼症状に限定されているものを「眼筋型MG」と呼びます。',
