    //         commentary: 'splenectomy(脾臓摘出術)が適応となる貧血疾患を問う問題。答えは複数。Hereditary spherocytosis(遺伝性球状赤血球症)は本邦で最も頻度の高い遺伝性溶血性貧血の一つで、その約70%を占める。5～10万人に1人の頻度である。常染色体優性遺伝形式をとるが常染色体劣性遺伝や孤発例も存在する。遺伝性楕円赤血球症は赤血球膜異常症の約10%程度を占め、基本的に常染色体優性遺伝形式をとり貧血は比較的軽症が多い。治療法は(貧血が重症の場合は)脾臓摘出術が唯一の治療法である。脾臓摘出術と同時に胆嚢摘出術を施行することが多い。 Glucose-6-phosphate dehydrogenase(グルコース-6-リン酸脱水素酵素欠損症)ではヘモグロビンの安定性を維持するために重要な酵素G6PDの産生に関与する遺伝子に異常があるため、血球中に十分なG6PDが存在しません。多くの場合、治療の必要はなく、支持療法・溶血を引き起こす薬剤の中止・既知の誘引の回避などによって赤血球数が市場に戻るのを待つだけです。重症の場合には、酸素投与や輸液などの治療を受けるために入院が必要になることがあります。 輸血が必要になることもあります。 Iron-deficiency anemia(鉄欠乏性貧血)は、体内鉄の欠乏によってヘモグロビン合成が低下して発症する疾患で、貧血の中で最も頻度が高い。病態は平均赤血球容積(MCV)が低値(80fl未満)となる小球性低色素性貧血を呈する。第一選択はステロイド投与であるが、ステロイドが効かない場合は脾臓摘出術も考慮される。 Autoimmune hemolytic anemia(自己免疫性溶血性貧血)は、自身の赤血球に結合する 自己抗体 (蛋白)ができて、赤血球が異常に早く破壊されておこる貧血です。いろいろな病型があり、原因もさまざまです。体温付近(37度)で抗体の結合が強いものを温式、体温以下（特に4度）で結合が強いものを冷式と呼びます。赤血球に自己抗体が結合し、補体(蛋白)と共同して血管内で赤血球を破壊するものと、自己抗体や補体を介して主に脾臓で破壊されるものがあります。治療法として、温式自己免疫性溶血性貧血には、副腎皮質ステロイドが有効です。手術による脾臓(赤血球を破壊している臓器)の摘出や免疫抑制薬も補助的な手段として行われます。貧血が強いときは輸血を行うこともあります。 Paroxysmal nocturnal hemoglobinuria(PNH:夜間発作性血色素尿症)は、赤血球が血管内で異常に早く破壊されておこる貧血で、早朝の ヘモグロビン尿 （コーラ色）を特徴とします。血液中の白血球、赤血球、血小板のすべてが減少する再生不良性貧血という疾患や、できた血球の数が少ないだけでなく、質も悪くなる骨髄 異形成 症候群という疾患と密接に関係しており、時に合併・相互移行します。この病気を完全に治すには骨髄移植しかありませんが、一般には経過の長い病気なので、重症の患者さんに対して行われます。溶血に対しては、これまで副腎皮質ステロイドが汎用されてきましたが、補体に対する 抗体薬 （エクリズマブ、ラブリズマブ）や阻害薬（ペグセタコプラン）が開発され、PNHの溶血に対する劇的な抑制効果が示されています。貧血が強ければ輸血も行います。血栓症があればその治療が必要ですし、再生不良性貧血の症状が強ければ、免疫抑制薬などの治療が有効です。',
